import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import DualListBox from "react-dual-listbox";
import SelectSearch from "react-select-search";
import Axios from "axios";
import { berhasil, gagal } from "../Alert";

const maptostate = (state) => {
  return {
    listDataUser: state.provinsi.listDataUser,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;
const serverMenu = process.env.REACT_APP_BACKEND_URL_SET_MENU;
class KelolaAkses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      options: [
        { label: "Data Provinsi", value: "Data Provinsi" },
        { label: "Data Kota", value: "Data Kota" },
        { label: "Data Area", value: "Data Area" },
        { label: "Daftar Rekening Bank", value: "Daftar Rekening Bank" },
        { label: "Data Lokasi", value: "Data Lokasi" },

        { label: "Data Salesman", value: "Data Salesman" },

        { label: "Data Supplier", value: "Data Supplier" },

        { label: "Data Customer", value: "Data Customer" },

        { label: "Data Kadar", value: "Data Kadar" },

        { label: "Data Klasifikasi Barang", value: "Data Klasifikasi Barang" },

        { label: "Kode Rongsok dan Ciok", value: "Kode Rongsok dan Ciok" },

        { label: "Data Transaksi", value: "Data Transaksi" },

        { label: "Saldo Awal", value: "Saldo Awal" },

        { label: "Manage user", value: "Manage user" },

        { label: "Pindah Barang", value: "Pindah Barang" },

        { label: "Mutasi Barang", value: "Mutasi Barang" },

        { label: "Mutasi Ciok", value: "Mutasi Ciok" },

        { label: "Kirim Masak", value: "Kirim Masak" },

        { label: "Terima Masak", value: "Terima Masak" },

        { label: "Penerimaan Supplier", value: "Penerimaan Supplier" },

        { label: "Return Supplier", value: "Return Supplier" },

        { label: "Tolakan Return Supplier", value: "Tolakan Return Supplier" },

        {
          label: "Transaksi Pengambilan Barcode",
          value: "Transaksi Pengambilan Barcode",
        },

        {
          label: "Close Pengambilan Barang Barcode",
          value: "Close Pengambilan Barang Barcode",
        },

        { label: "Transksi Timbang Barang", value: "Transksi Timbang Barang" },

        {
          label: "Batal Pengambilan Timbang Barang",
          value: "Batal Pengambilan Timbang Barang",
        },

        { label: "Transaksi Penjualan", value: "Transaksi Penjualan" },

        { label: "Return Penjualan", value: "Return Penjualan" },

        { label: "Rekap Bon Penjualan", value: "Rekap Bon Penjualan" },

        { label: "Kelola Uang Bank ", value: "Kelola Uang Bank " },

        { label: "Kelola Uang Kas", value: "Kelola Uang Kas" },

        { label: "Mutasi Cash Dan Bank", value: "Mutasi Cash Dan Bank" },

        { label: "Klasifikasi Supplier", value: "Klasifikasi Supplier" },

        { label: "Klasifikasi Customer", value: "Klasifikasi Customer" },

        { label: "Validasi Transfer", value: "Validasi Transfer" },

        { label: "Validasi Giro", value: "Validasi Giro" },

        { label: "Validasi Barang", value: "Validasi Barang" },

        { label: "Validasi Supplier", value: "Validasi Supplier" },

        { label: "Validasi Customer", value: "Validasi Customer" },

        { label: "Laporan Keuangan", value: "Laporan Keuangan" },

        { label: "Laporan Supplier", value: "Laporan Supplier" },

        { label: "laporan Stok", value: "laporan Stok" },

        { label: "laporan Salesman", value: "laporan Salesman" },

        { label: "laporan Customer", value: "laporan Customer" },

        { label: "Parameter", value: "Parameter" },

        { label: "Cetak History User", value: "Cetak History User" },

        { label: "Cetak Ulang Bukti", value: "Cetak Ulang Bukti" },

        { label: "Jual LM", value: "Jual LM" },

        { label: "Beli LM", value: "Beli LM" },

        { label: "Validasi Stok Jual LM", value: "Validasi Stok Jual LM" },

        { label: "Validasi Stok Beli LM", value: "Validasi Stok Beli LM" },

        { label: "Bayar Jual LM", value: "Bayar Jual LM" },

        { label: "Bayar Beli LM", value: "Bayar Beli LM" },

        { label: "Totalan Timbangan", value: "Totalan Timbangan" },

        { label: "Totalan Barcode", value: "Totalan Barcode" },

        { label: "Selisih Timbangan", value: "Selisih Timbangan" },

        { label: "Selisih Timbang Rongsok", value: "Selisih Timbang Rongsok" },
      ],
      selected: [],
      user: [
        {
          value: "ADMIN",
          name: "ADMIN",
        },
        {
          value: "OCTAVIAN",
          name: "OCTAVIAN",
        },
      ],
      listDataUser: [],
    };
  }

  componentDidMount() {
    Axios.get(server + "user")
      .then((res) =>
        this.setState({
          listDataUser: res.data,
        })
      )
      .catch(() =>
        gagal("Sepertinya ada gangguan,Mohon Check koneksi dan Refresh halaman")
      );
  }
  handleSubmit(data) {
    console.log(data);
  }
  onChange = (selectedValues) => {
    console.log(selectedValues);
  };
  onSubmitUser(data) {
    this.setState({
      userIdSelected: data,
    });
  }
  sendAkses() {
    var data = this.state.selected || [];
    var daftarArray = [];
    data.forEach((data) => {
      var hasil = {
        nama: data,
        status: true,
      };
      daftarArray.push(hasil);
    });
    var simpan = {
      userId: this.state.userIdSelected,
      daftarMenu: daftarArray,
    };
    console.log(JSON.stringify(simpan));
    Axios.post(serverMenu + "User/Authorization", simpan)
      .then(() => berhasil("Akses telah disimpan"))
      .catch(() => gagal("Sepertinya ada gangguan, periksa koneksi anda"));
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/ayugold.com/manageUser">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="card">
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12 mb-3">
                <h1>Pengaturan Hak Akses User</h1>
              </div>
              <div className="col-lg-4">
                <label htmlFor="">Silahkan pilih User</label>
                <SelectSearch
                  options={this.state.listDataUser.map((list) => {
                    var hasil = {
                      value: list.userId,
                      name: list.userName,
                    };
                    return hasil;
                  })}
                  search
                  placeholder="Silahkan Pilih"
                  onChange={(data) => this.onSubmitUser(data)}
                />
              </div>
              <div className="col-lg-8"></div>
              <div className="col-lg-6 mt-5">
                <div className="text-center">
                  <h3>Daftar menu yang ada</h3>
                </div>
              </div>
              <div className="col-lg-6 mt-5">
                <div className="text-center">
                  <h3>Daftar menu yang dipilih</h3>
                </div>
              </div>
              <div className="col-lg-12 " style={{ height: 500 }}>
                <DualListBox
                  canFilter
                  alignActions="top"
                  options={this.state.options}
                  selected={this.state.selected}
                  onChange={(selected) => {
                    this.setState({ selected });
                  }}
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    onClick={() => this.sendAkses()}
                  >
                    Simpan
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(KelolaAkses);
